#home{
    .word{
        font-size:50pt;
        text-align:center;
        display:block;
        position:relative;
        font-size:100pt;
        color: rgba(0,0,0,.8);


        .letter{
            letter-spacing:-45px;
            opacity:0;
        }
    }

    .home-title{
        @extend .transition;
        position:fixed;
        margin:auto;
        display:block;
        left:0;
        right:0;
        text-align:center;
        color:white;
        font-size:100px;
    }

    h1, h4{
        @extend .bpp-regular;
    }

    #home-title-1{
        z-index:$z_index_title_1;
    }

    #home-title-2{
        margin:auto;
        display:block;
        left:0;
        right:0;
        top:0;
        text-align:center;
        color:white;
        font-size:100px;
        z-index:$z_index_title_2;
        position:absolute;
    }

    #home-title-3{
        display:none;
        z-index:$z_index_title_3;

        h1{
            font-size:100px;
        }

        h1:first-child{
            color:#323232;
        }
    }

    .full-box{
        height:100vh;
        display:block;
        background-position:center;
        background-size:cover;
        background-attachment:fixed;
        position:relative;
    }

    #box-1{
        background-image:url('../dist/images/box-1.jpg');
        z-index:$z_index_box_1;
    }

    #box-2{
        background-image:url('../dist/images/box-2.jpg');
        z-index:$z_index_box_2;
        overflow-y:hidden;
    }

    #box-3{
        background-image:url('../dist/images/box-3.jpg');

        @-webkit-keyframes hop{
            0%{
                bottom: 50px;
            }
            50%{
                bottom:100px;
            }
            100%{
                bottom:50px;
            }
        }

        @keyframes hop{
            0%{
                bottom: 70px;
            }
            10%{
                bottom:100px;
            }
            20%{
                bottom:70px;
            }
            100%{
                bottom:70px;
            }
        }

        #angle-down{
            position: fixed;
            bottom: 50px;
            right: 0;
            left: 0;
            color: white;
            text-align: center;
            animation: hop 8s infinite;
            cursor:pointer;
        }
    }

    #box-4{
        z-index:$z_index_box_4;
        background-color:white;
    }

    #box-5{
        z-index:$z_index_box_5;
        background-color:$primary;

        #contact-form-container{
            position:absolute;
            background-color:white;
            margin:auto;
            text-align:center;
            display:block;
            left:0;
            right:0;
            width:50%;
            border-radius:5px;
            -webkit-box-shadow: 0px 0px 33px 0px rgba(0,0,0,0.75);
            -moz-box-shadow: 0px 0px 33px 0px rgba(0,0,0,0.75);
            box-shadow: 0px 0px 33px 0px rgba(0,0,0,0.75);
            filter:blur(0px);
            @extend .transition;

            &.blur{
                filter:blur(2px);
            }

            .contact-form-header{
                margin-bottom:25px;
            }

            .svg{
                fill:$primary;
            }

            .icon-animate{
                position:absolute;
                top:0;
                left:15px;
                transition:ease all 2s;
                z-index:2;
            }
        }
    }
}
